.button {
  display: block;
  color: #ffffff;
  font-family: $accent-font;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  text-align: center;
  padding: 11px 28px;
  border: none;
  background-color: $primary-color;


  &--secondary {
    color: $main-text-color;
    background-color: $bg-color;

    @media (min-width: $tablet-width) {
      font-size: 20px;
      line-height: 20px;
      padding: 17px 27px;
    }
  }
}
