.product {
  position: relative;

  @media (min-width: $tablet-width) {
    background-color: $bg-color;
    padding: 0 37px 40px 37px;
  }

  @media (min-width: $desktop-width) {
    padding: 0 38px 40px 38px;
  }
}

.product--more {
  background: none;
  padding: 25px 20px 40px 20px;

  .product__title {
    text-align: center;
  }

  .product__description {
    text-align: center;
  }

  @media (min-width: $tablet-width) {
    border: 2px solid $bg-color;
    padding-top: 50px;

    .product__title {
      padding: 0 70px;
    }

    .product__description {
      padding: 0 30px;
    }

    @media (min-width: $desktop-width) {
      .product__title {
        padding: 0 30px;
      }

      .product__description {
        padding: 0 10px;
      }
    }
  }
}

.product__container {
  display: grid;
  grid-template-columns: 140px auto;
  grid-template-rows: auto auto;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: column;
    height: 100%;
  }
}

.product__img {
  grid-row: 1/3;
  margin-left: auto;
  margin-right: auto;
  align-self: center;

  @media (min-width: $tablet-width) {
    flex-grow: 1;
  }
}

.product__title {
  font-family: $accent-font;
  font-size: 16px;
  line-height: 20px;
  color: $card-title-color;
  font-weight: 400;
  text-transform: uppercase;
  margin: 0;

  @media (min-width: $tablet-width) {
    text-align: center;
    font-size: 20px;
    line-height: 24px;
    padding: 0 50px;
  }

  @media (min-width: $desktop-width) {
    padding: 0 10px;
  }
}

.product__description {
  margin: 14px 0 17px 0;
  font-size: 12px;
  line-height: 16px;

  @media (min-width: $tablet-width) {
    margin: 20px 0 32px 0;
    font-size: 16px;
    line-height: 20px;
  }

  @media (min-width: $desktop-width) {
    margin: 23px 0 32px 0;
  }
}

.product__description-row td {
  @media (min-width: $tablet-width) {
    border-bottom: 1px solid #cdcdcd;
  }
}

.product__description-data {
  text-align: right;
}


.product__button {
  grid-column: 1/3;
}


.product__plus {
  position: relative;
  padding: 14px 0;
  margin-bottom: 23px;

  &::before {
    position: absolute;
    display: block;
    content: "";
    width: 30px;
    height: 2px;
    background-color: #d3d3d3;
    top: 14px;
    left: calc(50% - 15px);
  }

  &::after {
    position: absolute;
    display: block;
    content: "";
    width: 30px;
    height: 2px;
    background-color: #d3d3d3;
    transform: rotate(90deg);
    top: 15px;
    left: calc(50% - 15px);
  }
}
